import axios from 'axios';
import type { Paciente } from '../interfaces/Paciente';
import authHeader from './auth-header';

// const API_URL = 'http://localhost:8080/';
const API_URL = '/api/';
class PacienteService {
    async createPaciente(paciente: Paciente) {
        const plainPaciente = JSON.parse(JSON.stringify(paciente));
        console.log('paciente', plainPaciente);
        return await axios.post(API_URL + `pacientes/registrar`, plainPaciente, {
            headers: {
                ...authHeader(),
                'Content-Type': 'application/json',
            },
        });
    }
    async editPaciente(paciente: Paciente) {
        const plainPaciente = JSON.parse(JSON.stringify(paciente));
        console.log('PacienteEdit', plainPaciente);
        return await axios.put(API_URL + `pacientes/actualizar`, plainPaciente, {
            headers: {
                ...authHeader(),
                'Content-Type': 'application/json',
            },
        });
    }
}

export default new PacienteService();
