<template>
    <div id="modal" class="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center">
        <div class="bg-card p-5 rounded-lg w-full max-w-lg">
            <header class="flex items-center justify-center rounded p-4 bg-blue-900 text-white">
                <h1 class="text-lg font-semibold">Agendar Consulta</h1>
            </header>
            <main class="flex flex-col flex-1 space-y-6">
                <section>
                    <h2 class="text-lg font-semibold text-[#003366] mt-5">Participantes</h2>
                    <div class="space-y-4">
                        <div class="space-y-2 form-group">
                            <select
                                v-model="consultaRequest.nombrePaciente"
                                required
                                id="patient-name"
                                class="block w-full px-3 py-2 bg-white border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                                :class="{ 'border-red-500': v$.consultaRequest.nombrePaciente.$error }"
                            >
                                <option value="" disabled selected hidden></option>
                                <option v-for="paciente in pacientes" :key="paciente.id" :value="paciente.nombre">
                                    {{ paciente.nombre }}
                                </option>
                            </select>
                            <label for="patient-name" class="block text-sm font-medium text-gray-700 form-label">Nombre del paciente</label>
                            <span v-if="v$.consultaRequest.nombrePaciente.$error" class="flex font-sans text-sm text-red-600"
                                >Este campo es requerido</span
                            >
                        </div>
                        <div class="space-y-2 form-group">
                            <select
                                v-model="consultaRequest.nombreMedico"
                                required
                                id="doctor-name"
                                class="block w-full px-3 py-2 bg-white border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                                :class="{ 'border-red-500': v$.consultaRequest.nombreMedico.$error }"
                            >
                                <option value="" disabled selected hidden></option>
                                <option v-for="medico in medicos" :key="medico.id" :value="medico.nombre">
                                    {{ medico.nombre }}
                                </option>
                            </select>
                            <label for="doctor-name" class="block text-sm font-medium text-gray-700 form-label">Nombre del médico</label>
                            <span v-if="v$.consultaRequest.nombreMedico.$error" class="flex font-sans text-sm text-red-600"
                                >Este campo es requerido</span
                            >
                        </div>
                    </div>
                </section>
                <!-- Sección de fecha y horario -->
                <section>
                    <h2 class="text-lg font-semibold text-[#003366]">Fecha y horario</h2>
                    <div class="space-y-4">
                        <div>
                            <div class="mt-0 form-group">
                                <input
                                    required
                                    v-model="consultaRequest.fecha"
                                    type="date"
                                    id="appointment-date"
                                    class="block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                                    :class="{ 'border-red-500': v$.consultaRequest.fecha.$error }"
                                />
                                <label for="appointment-date" class="block text-sm font-medium text-gray-700 pl-3 form-label-date"
                                    >Fecha de la consulta</label
                                >
                                <span v-if="v$.consultaRequest.fecha.$error" class="flex font-sans text-sm text-red-600"
                                    >Este campo es requerido</span
                                >
                            </div>
                        </div>
                        <div class="space-y-2 form-group">
                            <select
                                required
                                v-model="horaRequest"
                                id="appointment-time"
                                class="block w-full px-3 py-2 bg-white border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                                :class="{ 'border-red-500': v$.horaRequest.$error }"
                            >
                                <option v-for="hora in horas" :key="hora" :value="hora">
                                    {{ hora }}
                                </option>
                            </select>
                            <label for="appointment-time" class="block text-sm font-medium text-gray-700 form-label"
                                >Horario de la consulta</label
                            >
                            <span v-if="v$.horaRequest.$error" class="flex font-sans text-sm text-red-600">Este campo es requerido</span>
                        </div>
                    </div>
                </section>
                <!-- Botones de acción -->
                <div class="flex flex-col space-y-4">
                    <button
                        @click="handleSubmit(consultaRequest)"
                        class="bg-[#003366] text-white p-2 border rounded hover:bg-blue-900 active:bg-white active:text-[#003366] active:border-[#003366] active:border"
                    >
                        Agendar consulta
                    </button>
                    <button
                        @click="$emit('close')"
                        class="border border-[#003366] text-[#003366] p-2 rounded active:bg-[#003366] hover:bg-primary/10 active:text-white"
                    >
                        Cancelar
                    </button>
                </div>
            </main>
        </div>
    </div>
</template>

<script setup lang="ts">
import { defineEmits, ref, watch, defineProps } from 'vue';
import { Consulta } from '../../login/interfaces/Consulta';
import { Medico } from '../../login/interfaces/Medico';
import { Paciente } from '../../login/interfaces/Paciente';
import ConsultaService from '../../login/helper/ConsultaService';
import { format } from 'date-fns';
import { fromZonedTime } from 'date-fns-tz';
import { ConsultaRequest } from '../../login/interfaces/ConsultaRequest';
import useVuelidate from '@vuelidate/core';
import { required } from '@vuelidate/validators';

const props = defineProps<{
    medicos: Medico[];
    pacientes: Paciente[];
}>();

const horaRequest = ref('');
const consultaRequest = ref<ConsultaRequest>({
    nombreMedico: '',
    nombrePaciente: '',
    fecha: '',
});

const rules = {
    consultaRequest: {
        nombrePaciente: { required },
        nombreMedico: { required },
        fecha: { required },
    },
    horaRequest: { required },
};
const v$ = useVuelidate(rules, { consultaRequest, horaRequest });

const horas = ['07:00', '08:00', '09:00', '10:00', '11:00', '12:00', '13:00', '14:00', '15:00', '16:00', '17:00', '18:00', '19:00'];
// const appointmentDate = ref('');
// const appointmentTime = ref('');
// // Observar cambios en la fecha y hora
// watch([appointmentDate, appointmentTime], ([newDate, newTime]) => {
//     if (newDate && newTime) {
//         // Combinar fecha y hora en el formato correcto
//         const dateTime = new Date(`${newDate}T${newTime}:00`);
//         const utcDateTime = fromZonedTime(dateTime, 'America/Mexico_City'); // Ajusta la zona horaria según tu ubicación
//         consultaRequest.value.fecha = format(utcDateTime, "yyyy-MM-dd'T'HH:mm:ss.SSS'Z'");
//     }
// });
const emit = defineEmits(['consultaCreated', 'close']);

async function handleSubmit(consulta: ConsultaRequest) {
    try {
        const isValid = await v$.value.$validate();
        if (!isValid) {
            // Formulario no válido, muestra errores
            return;
        }
        if (consultaRequest.value.fecha && horaRequest.value) {
            // Combinar fecha y hora en el formato correcto
            const dateTime = new Date(`${consultaRequest.value.fecha}T${horaRequest.value}:00`);
            const utcDateTime = fromZonedTime(dateTime, 'America/Mexico_City'); // Ajusta la zona horaria según tu ubicación
            consultaRequest.value.fecha = format(utcDateTime, "yyyy-MM-dd'T'HH:mm:ss.SSS'Z'");

            const response = await ConsultaService.createConsulta(consulta);
            const createdConsulta: Consulta = response.data.body;
            console.log('consulta:', createdConsulta);
            emit('consultaCreated', createdConsulta);
            emit('close');
        }
    } catch (error) {
        console.error('Error al crear la consulta:', error);
        // Manejar el error (mostrar un mensaje al usuario, etc.)
    }
}
</script>

<style scoped>
.form-input:focus + .form-label,
.form-input:not(:placeholder-shown) + .form-label {
    transform: translateY(-12px) scale(0.7);
    transform-origin: left top;
    color: hsl(var(--muted-foreground));
}

.form-label {
    position: absolute;
    top: 3px;
    left: 15px;
    transition:
        transform 0.5s,
        color 0.3s;
}

.form-label-date {
    position: absolute;
    top: -2px;
    left: 2px;
    font-size: 60%;
    color: hsl(var(--muted-foreground));
}

.form-group {
    position: relative;
    display: grid;
    /* gap: 20px;  */
}

.form-group select:focus-within + .form-label,
.form-group select:valid + .form-label {
    transform: translateY(-60%) scale(0.7);

    transform-origin: left top;
    color: hsl(var(--muted-foreground));
}
</style>
