import { toRaw } from 'vue';
import type { Consulta } from '../interfaces/Consulta';
import axios from 'axios';
import authHeader from './auth-header';
import type { ConsultaRequest } from '../interfaces/ConsultaRequest';

// const API_URL = 'http://localhost:8080/';
const API_URL = '/api/';
class ConsultaService {
    async editConsulta(consulta: Consulta) {
        console.log('datosEnviar: ', consulta);
        return await axios.put(API_URL + `consultas/actualizar`, consulta, {
            headers: {
                ...authHeader(),
                'Content-Type': 'application/json',
            },
        });
    }
    async createConsulta(consulta: ConsultaRequest) {
        console.log('datosEnviar: ', consulta);
        return await axios.post(API_URL + `consultas/registrar`, consulta, {
            headers: {
                ...authHeader(),
                'Content-Type': 'application/json',
            },
        });
    }
    async cancelarConsulta(consulta: Consulta, motivo: any) {
        console.log('datosEnviar: ', motivo);
        return await axios.delete(API_URL + `consultas/cancelar`, {
            data: { idConsulta: consulta.id, motivo: motivo },
            headers: authHeader(),
        });
    }
}
export default new ConsultaService();
