const menuOpen = ref(false);
const toggleMenu = () => {
    menuOpen.value = !menuOpen.value;
    console.log('stateAuth', store.state.authentication);
};
function regresar() {
    router.push({ name: 'pacientes' });
}
const pacienteRequest = ref<Paciente>({
    id: 0,
    nombre: '',
    documento: '',
    email: '',
    telefono: '',
    direccion: {
        calle: '',
        ciudad: '',
        complemento: '',
        estado: '',
        numero: '',
        postal: '',
    },
});

const rules = {
    pacienteRequest: {
        nombre: { required },
        documento: { required },
        email: { required },
        telefono: { required },
        direccion: {
            calle: { required },
            ciudad: { required },
            estado: { required },
            postal: { required },
        },
    },
};
const v$ = useVuelidate(rules, { pacienteRequest });
const emit = __emit;

async function handleSubmit() {
    const isValid = await v$.value.$validate();
    if (!isValid) {
        // Formulario no válido, muestra errores
        return;
    }
    try {
        const response = await PacienteService.createPaciente(pacienteRequest.value);
        const newPaciente: Paciente = response.data;
        alert('Exito Paciente registrado');
        emit('pacienteNew', newPaciente);
        // props.onMedicoCreated(newMedico);
    } catch (e) {
        console.error('Error creating paciente:', e);
        if (axios.isAxiosError(e)) {
            if (!e.response) {
                Swal.fire({
                    title: 'Error!',
                    text: 'Ha ocurrido un error desconocido',
                    icon: 'error',
                    confirmButtonText: 'Ok',
                });
            } else if (e.response.status === 409) {
                Swal.fire({
                    title: 'Error: ' + e.response?.data[0].campo,
                    text: e.response?.data[0].error,
                    icon: 'error',
                    confirmButtonText: 'Ok',
                });
            } else if (e.response.status === 400) {
                Swal.fire({
                    title: 'Error!',
                    text: e.response?.data,
                    icon: 'error',
                    confirmButtonText: 'Ok',
                });
            }
        }
    }
}

