<script setup lang="ts">
import router from '@/router';
import store from '@/store';
import { ref, defineEmits, defineProps } from 'vue';
import { MedicoRequest } from '../../login/interfaces/MedicoRequest';
import { Medico } from '../../login/interfaces/Medico';
import MedicoService from '../../login/helper/MedicoService';
import { required } from '@vuelidate/validators';
import useVuelidate from '@vuelidate/core';
import axios from 'axios';
import Swal from 'sweetalert2';

const menuOpen = ref(false);
const toggleMenu = () => {
    menuOpen.value = !menuOpen.value;
    console.log('stateAuth', store.state.authentication);
};
function regresar() {
    router.push({ name: 'medicos' });
}
const medicoRequest = ref<MedicoRequest>({
    nombre: '',
    especialidad: '',
    documento: '',
    email: '',
    telefono: '',
    direccion: {
        calle: '',
        ciudad: '',
        complemento: '',
        estado: '',
        numero: '',
        postal: '',
    },
});
const rules = {
    medicoRequest: {
        nombre: { required },
        especialidad: { required },
        documento: { required },
        email: { required },
        telefono: { required },
        direccion: {
            calle: { required },
            ciudad: { required },
            estado: { required },
            postal: { required },
        },
    },
};
const v$ = useVuelidate(rules, { medicoRequest });
defineProps<{
    onMedicoCreated: (medico: Medico) => void;
}>();
const emit = defineEmits(['medicNew']);

async function handleSubmit() {
    const isValid = await v$.value.$validate();
    if (!isValid) {
        // Formulario no válido, muestra errores
        return;
    }
    try {
        const response = await MedicoService.createMedico(medicoRequest.value);
        const newMedico: Medico = response.data;
        alert('Exito Medico registrado');
        emit('medicNew', newMedico);
        // props.onMedicoCreated(newMedico);
    } catch (e) {
        console.error('Error creating medico:', e);
        if (axios.isAxiosError(e)) {
            if (!e.response) {
                Swal.fire({
                    title: 'Error!',
                    text: 'Ha ocurrido un error desconocido',
                    icon: 'error',
                    confirmButtonText: 'Ok',
                });
            } else if (e.response.status === 409) {
                Swal.fire({
                    title: 'Error: ' + e.response?.data[0].campo,
                    text: e.response?.data[0].error,
                    icon: 'error',
                    confirmButtonText: 'Ok',
                });
            } else if (e.response.status === 400) {
                Swal.fire({
                    title: 'Error!',
                    text: e.response?.data,
                    icon: 'error',
                    confirmButtonText: 'Ok',
                });
            }
        }
    }
}
</script>

<style scoped>
.bg-card {
