    transform-origin: left top;
}

.form-label {
    position: absolute;
    top: 15px;
    left: 5px;
    transition:
        transform 0.5s,
        color 0.3s;
}

.form-group {
    position: relative;
    display: grid;
}

.form-group select:focus-within + .form-label,
.form-group select:valid + .form-label {
    transform: translateY(-60%) scale(0.7);
    transform-origin: left top;
}
</style>

