const menuOpen = ref(false);
const toggleMenu = () => {
    menuOpen.value = !menuOpen.value;
    console.log('stateAuth', store.state.authentication);
};
function regresar() {
    router.push({ name: 'medicos' });
}
const medicoRequest = ref<MedicoRequest>({
    nombre: '',
    especialidad: '',
    documento: '',
    email: '',
    telefono: '',
    direccion: {
        calle: '',
        ciudad: '',
        complemento: '',
        estado: '',
        numero: '',
        postal: '',
    },
});
const rules = {
    medicoRequest: {
        nombre: { required },
        especialidad: { required },
        documento: { required },
        email: { required },
        telefono: { required },
        direccion: {
            calle: { required },
            ciudad: { required },
            estado: { required },
            postal: { required },
        },
    },
};
const v$ = useVuelidate(rules, { medicoRequest });

const emit = __emit;

async function handleSubmit() {
    const isValid = await v$.value.$validate();
    if (!isValid) {
        // Formulario no válido, muestra errores
        return;
    }
    try {
        const response = await MedicoService.createMedico(medicoRequest.value);
        const newMedico: Medico = response.data;
        alert('Exito Medico registrado');
        emit('medicNew', newMedico);
        // props.onMedicoCreated(newMedico);
    } catch (e) {
        console.error('Error creating medico:', e);
        if (axios.isAxiosError(e)) {
            if (!e.response) {
                Swal.fire({
                    title: 'Error!',
                    text: 'Ha ocurrido un error desconocido',
                    icon: 'error',
                    confirmButtonText: 'Ok',
                });
            } else if (e.response.status === 409) {
                Swal.fire({
                    title: 'Error: ' + e.response?.data[0].campo,
                    text: e.response?.data[0].error,
                    icon: 'error',
                    confirmButtonText: 'Ok',
                });
            } else if (e.response.status === 400) {
                Swal.fire({
                    title: 'Error!',
                    text: e.response?.data,
                    icon: 'error',
                    confirmButtonText: 'Ok',
                });
            }
        }
    }
}

