import store from '@/store';
import LoginForm from '@/views/LoginForm.vue';
import { createRouter, createWebHashHistory, RouteRecordRaw } from 'vue-router';
import RegisterMedicView from '@/components/page/home/medicos/RegisterMedicView.vue';
import RegisterPacienteView from '@/components/page/home/pacientes/RegisterPacienteView.vue';
import RegisterConsultaModal from '@/components/page/home/consultas/RegisterConsultaModal.vue';

const routes: Array<RouteRecordRaw> = [
    {
        path: '/',
        name: 'login',
        component: LoginForm,
        meta: { guest: true }, // Añadimos una meta propiedad para indicar que esta ruta es para invitados
    },
    {
        path: '/home',
        name: 'home',
        component: () => import('../views/HomeView.vue'),
        meta: { requiresAuth: true },
    },

    {
        path: '/medicos',
        name: 'medicos',
        component: () => import('../views/MedicosView.vue'),
        meta: { requiresAuth: true },
    },
    {
        path: '/pacientes',
        name: 'pacientes',
        component: () => import('../views/PacientesView.vue'),
        meta: { requiresAuth: true },
    },

    {
        path: '/consultas',
        name: 'consultas',
        component: () => import('../views/ConsultasView.vue'),
        meta: { requiresAuth: true },
    },
    {
        path: '/register-medic-view',
        name: 'registerMedic',
        component: RegisterMedicView,
        props: (route) => ({ onMedicoCreated: route.params.onMedicoCreated }),
    },
    {
        path: '/register-paciente-view',
        name: 'registerPaciente',
        component: RegisterPacienteView,
    },
    {
        path: '/register-consulta-view',
        name: 'registerConsulta',
        component: RegisterConsultaModal,
    },
];

const router = createRouter({
    history: createWebHashHistory(),
    routes,
});

router.beforeEach((to, from, next) => {
    const requiresAuth = to.matched.some((record) => record.meta.requiresAuth);
    const guestOnly = to.matched.some((record) => record.meta.guest);
    const isAuthenticated = store.getters.isAuthenticated;

    if (requiresAuth && !isAuthenticated) {
        next({ name: 'login' });
    } else if (guestOnly && isAuthenticated) {
        next({ name: 'home' }); // Redirigir al usuario autenticado
    } else {
        next();
    }
});
export default router;
