<template>
    <div class="bg-zinc-100 flex items-center justify-center min-h-screen">
        <div class="bg-white border border-zinc-300 shadow-lg rounded-lg max-w-md w-full mx-4 p-6">
            <div class="flex items-center justify-center mb-6">
                <div>
                    <h1 class="text-2xl font-bold text-blue-600">Voll</h1>
                </div>
            </div>

            <div class="mb-8 text-center">
                <h2 class="text-lg font-semibold text-zinc-700">{{ formTitle }}</h2>
            </div>
            <form @submit.prevent="handleSubmit">
                <div class="mb-4 containerlabel" v-if="authData.formType">
                    <label for="username" class="textlabel block text-sm font-medium text-zinc-700">Correo Electrónico</label>
                    <input
                        required
                        v-model="authData.email"
                        type="email"
                        id="email"
                        name="email"
                        placeholder="correo@ejemplo.com"
                        class="mt-1 block w-full px-3 py-2 border border-zinc-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm"
                    />
                </div>
                <div class="mb-4 containerlabel">
                    <label for="username" class="textlabel block text-sm font-medium text-zinc-700">Usuario</label>
                    <input
                        required
                        v-model="authData.username"
                        type="text"
                        id="username"
                        name="username"
                        placeholder="Username"
                        class="mt-1 block w-full px-3 py-2 border border-zinc-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm"
                    />
                </div>
                <div class="mb-6 containerlabel">
                    <label for="password" class="textlabel block text-sm font-medium text-zinc-700">Contraseña</label>
                    <input
                        required
                        v-model="authData.password"
                        type="password"
                        id="password"
                        name="password"
                        placeholder="********"
                        class="mt-1 block w-full px-3 py-2 border border-zinc-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm"
                    />
                </div>
                <div class="mb-6 containerlabel" v-if="authData.formType">
                    <label for="password" class="textlabel block text-sm font-medium text-zinc-700">Confirmar Contraseña</label>
                    <input
                        required
                        v-model="authData.confirmPass"
                        type="password"
                        id="password"
                        name="password"
                        placeholder="********"
                        @blur="v$.authData.confirmPass.$touch()"
                        class="mt-1 block w-full px-3 py-2 border border-zinc-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm"
                    />
                    <span v-if="v$.authData.confirmPass.$error && v$.authData.confirmPass.sameAsPassword.$invalid" class="text-red-500 text-xs">
                        {{ v$.authData.confirmPass.sameAsPassword.$message }}
                    </span>
                </div>
                <div>
                    <button
                        v-if="sessionValue"
                        type="submit"
                        class="w-full py-2 px-4 bg-blue-600 text-white rounded-md shadow-sm active:bg-white active:border-blue-600 active:text-blue-600 hover:bg-blue-500 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
                    >
                        {{ sessionText }}
                    </button>
                    <button
                        v-else
                        v-on:click="router.push('home')"
                        class="w-full py-2 px-4 bg-blue-600 text-white rounded-md shadow-sm active:bg-white active:border-blue-600 active:text-blue-600 hover:bg-blue-500 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
                    >
                        {{ sessionText }}
                    </button>
                </div>
            </form>

            <div class="mt-8 text-center">
                <span class="text-zinc-600">{{ formSwitchText }}</span>
                <button v-on:click="toggleFormType" class="text-blue-600 hover:underline">
                    {{ formSwitchButtonText }}
                </button>
            </div>
        </div>
    </div>
</template>

<script setup lang="ts">
import { useRouter } from 'vue-router';
import { AuthData } from '../components/page/login/interfaces/AuthData';
import { reactive, computed, onMounted, watch } from 'vue';
import AuthService from '../components/page/login/helper/authService';
import store from '@/store';
import { helpers, required, sameAs } from '@vuelidate/validators';
import useVuelidate from '@vuelidate/core';
import axios from 'axios';
import Swal from 'sweetalert2';
const authData = reactive<AuthData>({
    email: '',
    username: '',
    password: '',
    confirmPass: '',
    formType: false, // true para registro, false para inicio de sesión
});
const rules = {
    authData: {
        password: { required },
        confirmPass: {
            required,
            sameAsPassword: helpers.withMessage('Las contraseñas no coinciden', sameAs(computed(() => authData.password))),
        },
    },
};
const v$ = useVuelidate(rules, { authData });

const sessionValue = store.getters.authentication;
const sessionText = computed(() => (store.state.authentication ? 'Go Session' : formTitle.value));
const formTitle = computed(() => (authData.formType ? 'Registrarse' : 'Iniciar sesión'));
const formSwitchText = computed(() => (authData.formType ? 'Ya tengo cuenta!' : '¿No tienes una cuenta?'));
const formSwitchButtonText = computed(() => (authData.formType ? 'Iniciar Session' : 'Registrarse'));
const router = useRouter();
function toggleFormType() {
    authData.formType = !authData.formType;
}

const handleSubmit = async () => {
    if (authData.formType) {
        const isFormValid = await v$.value.$validate();
        if (!isFormValid) {
            return;
        }
        // Lógica de registro
        try {
            await AuthService.register(authData.username, authData.email, authData.password);
            toggleFormType(); // Cambiar a formulario de inicio de sesión después de registrarse
            alert('Registro exitoso, por favor inicia sesión.');
        } catch (error) {
            console.error('Error en el registro:', error);
        }
    } else {
        // Lógica de inicio de sesión
        try {
            const response = await AuthService.login(authData.username, authData.password);
            store.commit('SET_TOKEN', response.jwTtoken);
            store.commit('SET_AUTHENTICATION', true);
            router.push({ name: 'home' });
        } catch (error) {
            console.error('Error en el inicio de sesión:', error);
            if (axios.isAxiosError(error)) {
                if (!error.response) {
                    Swal.fire({
                        title: 'Error!',
                        text: error.response,
                        icon: 'error',
                        confirmButtonText: 'Ok',
                    });
                } else if (error.response.status >= 401) {
                    Swal.fire({
                        title: 'Ups!',
                        text: error.response.data,
                        icon: 'info',
                        confirmButtonText: 'Ok',
                    });
                }
            }
        }
    }
};
onMounted(() => {
    const token = localStorage.getItem('token');
    const authentication = localStorage.getItem('auth');
    if (token && authentication === 'true') {
        store.commit('SET_TOKEN', token);
        store.commit('SET_AUTHENTICATION', true);
    }
});
</script>
<style scope="" lang="css">
.containerlabel {
    text-align-last: left;
}
.textlabel {
    font-weight: 600;
}
</style>
