import { createStore } from 'vuex';

export default createStore({
    state: {
        token: localStorage.getItem('token') || null,
        authentication: localStorage.getItem('token') ? true : false,
    },

    mutations: {
        SET_TOKEN(state, token) {
            state.token = token;
            localStorage.setItem('token', token);
        },
        CLEAR_TOKEN(state) {
            state.token = null;
            localStorage.removeItem('token');
        },
        CLEAR_AUTH(state) {
            state.authentication = false;
            localStorage.removeItem('auth');
        },
        SET_AUTHENTICATION(state, authentication) {
            state.authentication = authentication;
            localStorage.setItem('auth', authentication);
        },
    },
    actions: {
        logout({ commit }) {
            commit('CLEAR_TOKEN');
            commit('CLEAR_AUTH');
        },
        // updatedAuthentication({ commit }, authentication) {
        //     commit('SET_AUTHENTICATION', (this.state.authentication = authentication));
        // },
    },
    getters: {
        token: (state) => state.token,
        isAuthenticated: (state) => state.authentication,
    },
    modules: {},
});
