import axios from 'axios';
import authHeader from './auth-header';
import type { Medico } from '../interfaces/Medico';
import type { MedicoRequest } from '../interfaces/MedicoRequest';
import { toRaw } from 'vue';

// const API_URL = 'http://localhost:8080/';
const API_URL = '/api/';
class MedicoService {
    async createMedico(medico: MedicoRequest) {
        const plainMedico = toRaw(medico);
        console.log('medico', plainMedico);
        return await axios.post(API_URL + `medicos/registrar`, plainMedico, {
            headers: {
                ...authHeader(),
                'Content-Type': 'application/json',
            },
        });
    }
    async editMedico(medico: Medico) {
        const plainMedico = JSON.parse(JSON.stringify(medico));
        console.log('medicoEdit', plainMedico);
        return await axios.put(API_URL + `medicos/actualizar`, plainMedico, {
            headers: {
                ...authHeader(),
                'Content-Type': 'application/json',
            },
        });
    }

    async desactivarMedico(medico: Medico) {
        console.log('medico a borrar', medico.id);
        return await axios.delete(API_URL + `medicos/desactivar/` + medico.id, { headers: authHeader() });
    }
}
export default new MedicoService();
